<template>
  <div id="singer">
    <div class="info" v-for="item in singer" :key="item.id" @click="linkTo(item)">
      <img class="img-size" :src="getImg(item)" alt="" />
      <div class="title">{{ item.name }}</div>
    </div>
    
    <div v-if="loadStatus" class="load-bottom">
      <text-loading />
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
  props: ['singer','loadStatus'],
  methods: {
    ...mapMutations({ setSingerInfo: 'SET_SINGERINFO' }),
    getImg(item) {
      return item.picUrl || item.img1v1Url
    },
    linkTo(item) {
      this.$router.push({
        path: 'singerDetail',
        query: {
          id:item.id
        }
      })
      this.setSingerInfo(item)
    }
  }
}
</script>

<style scoped lang="less">
#singer {
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  .info {
    width: 140px;
    height: 140px;
    margin: 40px 20px;
    .img-size {
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
    .title {
      text-align: center;
      padding-top: 10px;
    }
  }
}
</style>
