<template>
  <div id="search">
    <div class="header">
      <div class="bg"></div>
      <div class="search-box">
        <div class="input-box">
          <input type="text" class="text" placeholder="搜索音乐、MV、歌单、用户" v-model="keys" @keyup.enter="search(1)" />
          <span class="icon" @click="search()"></span>
        </div>
        <div class="hot">
          <span class="hot-search">热门搜索 :</span>
          <span class="item" v-for="(item, index) in hots" :key="index" @click="hotSearchItem(item.first)">{{ item.first }}</span>
        </div>
      </div>
    </div>
    <div class="limit">
      <div class="search-result">
        <div class="item" :class="{ active: type === 1 }" @click="changeType(1)">单曲</div>
        <div class="item" :class="{ active: type === 100 }" @click="changeType(100)">歌手</div>
        <div class="item" :class="{ active: type === 10 }" @click="changeType(10)">专辑</div>
        <div class="item" :class="{ active: type === 1014 }" @click="changeType(1014)">视频</div>
        <div class="item" :class="{ active: type === 1000 }" @click="changeType(1000)">歌单</div>
      </div>
      <songs-list v-if="type === 1" :songs="searchList"></songs-list>
      <singer v-if="type === 100" :singer="singer"></singer>
      <albums v-if="type === 10" :albums="albums"></albums>
      <videos v-if="type === 1014" :videos="videos"></videos>
      <song-sheet v-if="type === 1000" :songSheet="songSheet"></song-sheet>

      <el-pagination v-show="total > 24" hide-on-single-page layout="prev, pager, next" :total="total" id="pagination" @prev-click="pre" @next-click="next" @current-change="current" :current-page="cueerenPage">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import songsList from '@/components/common/ArtistList/ArtistList'
import singer from '@/components/common/singer/singer'
import albums from '@/components/common/albums/albums'
import videos from '@/components/common/videos/video'
import songSheet from '@/components/common/songSheet/songSheet'
import { allSearch, searchHot, searchSuggest } from '@/network/search'
import { SongsInfo, getSongDetail, videoInfo } from '@/network/detail'

export default {
  components: {
    songsList,
    singer,
    albums,
    videos,
    songSheet
  },
  data() {
    return {
      keys: '',
      limit: 24,
      type: 1,
      offset: 0,
      hots: '',
      searchList: [],
      total: 0,
      cueerenPage: 0,
      singer: [],
      albums: [],
      videos: [],
      songSheet: []
    }
  },
  watch: {
    $route(newstr, oldstr) {
      console.log(newstr)
      if (newstr.query.keywords !== oldstr.query.keywords) {
        this.keys = newstr.query.keywords
        this.search()
      }
    }
  },
  created() {
    this.keys = this.$route.query.keywords
    this.search()
    // 热搜
    searchHot().then(res => {
      this.hots = res.data.result.hots
    })
    // 搜索关键词
    searchSuggest(this.keys).then(res => {
      // console.log(res)
    })
  },
  methods: {
    search(type) {
      allSearch(this.keys, this.limit, this.offset, type).then(res => {
        switch (type) {
          case 1: {
            this.total = res.data.result.songCount
            this.getSongID(res.data.result.songs)
            break
          }
          case 100: {
            this.total = res.data.result.artistCount
            this.singer = res.data.result.artists
            break
          }
          case 10: {
            this.total = 0
  
            this.albums = res.data.result.albums
            console.log(this.albums);
            break
          }
          case 1014: {
            this.total = res.data.result.videoCount
            this.videos = this.normalizeVideo(res.data.result.videos)
            break
          }
          case 1000: {
            this.total = res.data.result.playlistCount
            this.songSheet = res.data.result.playlists
            break
          }
          default: {
            this.total = res.data.result.songCount
            this.getSongID(res.data.result.songs)
            break
          }
        }
      })
    },
    nextSongs(index) {
      switch (this.type) {
        case 1: {
          allSearch(this.keys, this.limit, (index - 1) * this.limit).then(res => {
            this.getSongID(res.data.result.songs)
          })
          break
        }
        case 100: {
          allSearch(this.keys, this.limit, (index - 1) * this.limit, this.type).then(res => {
            this.singer = res.data.result.artists
          })
          break
        }
        case 1014: {
          allSearch(this.keys, this.limit, (index - 1) * this.limit, this.type).then(res => {
            this.videos = this.normalizeVideo(res.data.result.videos)
            console.log(res)
          })
          break
        }
        case 1000: {
          allSearch(this.keys, this.limit, (index - 1) * this.limit, this.type).then(res => {
            this.songSheet = res.data.result.playlists
          })

          break
        }
      }
    },
    // 热门搜索
    hotSearchItem(info) {
      this.keys = info
      allSearch(info, this.limit).then(res => {
        this.getSongID(res.data.result.songs)
      })
    },

    // 获取歌曲ID
    getSongID(data) {
      let songId = []
      if (data) {
        data.forEach(item => {
          songId.push(item.id)
        })
      }
      this.getSongDetail(songId)
    },
    // 搜索类型
    changeType(type) {
      this.type = type
      if(type===1000){
        this.limit=27
      }
      this.search(type)
    },
    // 获得歌曲西详情
    getSongDetail(list) {
      let ids = list.join(',')
      getSongDetail(ids).then(res => {
        this.searchList = this.normalizeSongs(res.data.songs)
      })
    },
    // 处理mv
    normalizeVideo(list) {
      console.log(list);
     
      let videos = []
      list.forEach(item => {
        let info = new videoInfo(item)
        videos.push(info)
      })
   console.log(videos);
      return videos
    },
    // 处理歌曲信息
    normalizeSongs(list) {
      let songs = []
      if (list) {
        list.forEach(item => {
          let info = new SongsInfo(item)
          songs.push(info)
        })
      }
      return songs
    },

    // 分页
    pre(index) {
      this.cueerenPage = index
      this.nextSongs(index)
    },
    next(index) {
      this.cueerenPage = index
      this.nextSongs(index)
    },
    current(index) {
      this.cueerenPage = index
      this.nextSongs(index)
    }
  }
}
</script>

<style scoped lang="less">
.header {
  position: relative;
  .bg {
    height: 300px;
    background: url('../../assets/img/search/bg_search.jpg') no-repeat;
    background-size: 100% 100%;
  }

  .search-box {
    height: 100px;
    width: 700px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    .input-box {
      position: relative;
      font-size: 15px;
      .text {
        width: 100%;
        height: 50px;
        border: 1px solid #eee;
        border-radius: 3px;
        padding-left: 20px;
      }
      .icon {
        width: 30px;
        height: 50px;
        background: url('../../assets/img/header/search.png') no-repeat;
        background-size: 20px 20px;
        position: absolute;
        right: 0;
        top: 15px;
      }
    }
    .hot {
      margin: 0 auto;
      color: #fff;
      width: 500px;
      padding: 15px 20px;
      font-size: 14px;
      white-space: nowrap;
      overflow: hidden;
    
      .item {
        margin: 0 10px;
      }
      .item:hover {
        color: #31c27c;
      }
    }
  }
}
.search-result {
  display: flex;
  font-size: 16px;
  margin-top: 30px;
  height: 50px;
  line-height: 50px;
  .item {
    margin-right: 55px;
  }
  .active {
    color: #31c27c;
  }
}
#pagination {
  margin: 10px;
  display: flex;
  justify-content: center;
}
</style>
